<template>
<div class="main" >
<router-view></router-view> 
    <div class="container">
      <div class="content">
        <div class="toolbar">
          <div class="cname" @click="sortByName">名称
            <div :style="nameUpStyleObject" class="up" ></div>
            <div :style="nameDownStyleObject" class="down" ></div>
          </div>
          <div class="cprice" @click="sortByPrice">行情
            <div :style="cpriceUpStyleObject" class="up" ></div>
            <div :style="cpriceDownStyleObject" class="down" ></div>
          </div>
          <div class="crate" @click="sortByRate">涨幅
            <div :style="crateUpStyleObject" class="up" ></div>
            <div :style="crateDownStyleObject" class="down" ></div>
          </div>
        </div>
        <div class="smallTool">
          <button class="refresh" @click="refresh">{{refreshFlag?"刷新":"加载中"}}</button>
          <input v-model="searchInput" class="search" placeholder="输入要搜索的内容" />
        </div>
        <div class="search"><div class="icon"></div></div>

        <!-- show result -->
        <div class="items" v-for="item in searchFlag?resSearch:res"  @click.capture="navToDetail(item,$event)" data-src="item.id" >
          <div class="name">
            <div class="indexAndName">
              <div class="sname">{{item.rank}},{{item.id}}</div>
            </div>
            <div class="symbol">{{item.symbol}}</div>
            <div class="market">${{item.market_cap_usd|formatPrice}}</div>
          </div>
          <div class="price">￥{{item.price_cny|toFixed}}</div>
          <div :style="{'background-color':(colorSet?(item.percent_change_24h<0?'#009900':'#CC3300'):(item.percent_change_24h>0?'#009900':'#CC3300'))}" class="rate">{{item.percent_change_24h>0?("+"+item.percent_change_24h):item.percent_change_24h||'暂无'}}</div>
        </div>
        <!-- result finished！ -->
        <button v-show="!searchFlag" @click="loadMore" class="more">{{moreLoading?moreLoading:(searchEnd?"没有了":"更多")}}</button>
      </div>
      
    </div>
    <!-- <a @click="navToTop" href="javascript:void(0)" class="navTop"></a> -->
</div>
</template>

<script>
  import Detail from './Header'
  export default {
    name: 'main',
    components:{
      'my-detail':Detail
    }
    ,
    data () {
      return {
        msg: 'Welcome to Your Main',
        searchEnd:false,
        res:"",
        result:"",
        page:1,
        searchInput:"",
        searchFlag:false,
        resSearch:"",
        sortName:false,
        sortRate:false,
        sortPrice:false,
        moreLoading:'',
        refreshFlag:true,
        colorSet:false
      }
    },
    activated(){
        this.colorSet = localStorage.getItem('colorSet');
        if(this.colorSet=='false') this.colorSet=false;
        this.searchInput=""
        // console.log('activated '+this.colorSet)
    },
    methods:{
      navToDetail(i,e){
        var id = i.id;
        // console.log(id)
        this.$router.push({name:'Detail',params:{id:id}})
      },
      sortByName(){
        var that  = this;
        this.sortName = !this.sortName;
        this.res = this.res.sort(function(a,b){
          return that.sortName?(a.market_cap_usd - b.market_cap_usd):(b.market_cap_usd - a.market_cap_usd) ;
        })
      },
      sortByPrice(){
        var that  = this;
        this.sortPrice = !this.sortPrice;
        this.res = this.res.sort(function(a,b){
          return that.sortPrice? (a.price_usd - b.price_usd):(b.price_usd - a.price_usd) ;
        })
      },
      sortByRate(){
        var that  = this;
        this.sortRate = !this.sortRate;
        this.res = this.res.sort(function(a,b){
          return that.sortRate?(a.percent_change_24h - b.percent_change_24h):(b.percent_change_24h - a.percent_change_24h) ;
        })
      },
      loadMore(){
        // show loading page
        var that = this;
        this.moreLoading = 'loading...'
        setTimeout(function(){
          if(that.page>9){
            that.page = 10;
            that.searchEnd = true;
            that.moreLoading=""
            return;
          }
          that.res = that.res.concat(that.result.slice(20*that.page,20*(that.page+1)));
          that.page++;
          that.moreLoading=""
        }, 500);
        
      },
      refresh(){
        var vm = this;
        this.refreshFlag = false;
        axios.get('https://api.coinmarketcap.com/v1/ticker/?convert=CNY').then(function(response){
        // console.log(response);
        vm.result = response.data;
        vm.res = response.data.slice(0,20);
        vm.page = 1;
        localStorage.setItem('result',JSON.stringify(response.data));
        vm.refreshFlag = true;
      })
      },
      navToTop(){

      },
      scrollEvent(e){
        // console.log(e);
        // console.log(this.$el)
      }
    },
    filters:{
      toFixed(e){
        return parseFloat(e).toFixed(3)
      },
      formatPrice(n){
        var n = parseInt(n)
        var arr = (n+"").split("").reverse();
        var len= arr.length;
        var res = [];
        var i = 1;
        var z = 0;
        while(len>3*i){

        res = arr.splice(z+3*i++,0,',')
        z++;
        }
        return  arr.reverse().join("");
      }
    },
    created:function(){
      var that = this;
      function refreshCur(){
        that.refresh();
        // console.log('refreshed!')
        if(st){
          return ;
        }else{
          var st = setTimeout(refreshCur,5000);
        }
      }
      // refreshCur();
      // console.log(this.$el);
      this.colorSet = localStorage.getItem('colorSet');
      // console.log(this.colorSet);
      // this.$el.addEventListener('scroll',this.scrollEvent)
    }
    ,
    getResponse:function(){

    },
    beforeMount:function(){
      var vm = this;
      var url = 'https://api.coinmarketcap.com/v1/ticker/?convert=CNY';
      var local = localStorage.getItem('result');
      var resString = JSON.parse(local);
      if(resString){
        vm.result = resString;
        vm.res = resString.slice(0,20);
        // console.log('getLocalSucceed!')
        return;
      }
      axios.get(url).then(function(response){
        // console.log(response)
        vm.result = response.data;
        vm.res = response.data.slice(0,20);
        localStorage.setItem('result',JSON.stringify(response.data));
      })
    },
    computed:{
      nameUpStyleObject:function(){
        return {
          'border-top-color':this.sortName?"whitesmoke":"lightgrey"
        }
      },
      nameDownStyleObject:function(){
        return {
          'border-bottom-color':this.sortName?"lightgrey":"whitesmoke"
        }
      },
      cpriceUpStyleObject:function(){
        return {
          'border-top-color':this.sortPrice?"whitesmoke":"lightgrey"
        }
      },
      cpriceDownStyleObject:function(){
        return {
          'border-bottom-color':this.sortPrice?"lightgrey":"whitesmoke"
        }
      },
      crateUpStyleObject:function(){
        return {
          'border-top-color':this.sortRate?"whitesmoke":"lightgrey"
        }
      },
      crateDownStyleObject:function(){
        return {
          'border-bottom-color':this.sortRate?"lightgrey":"whitesmoke"
        }
      },
    },
    watch:{
      searchInput:function(v){
        // console.log(e)
        if(v==""){
          this.searchFlag = false;
        }else{
          this.searchFlag = true;
          this.resSearch = this.res.filter(function(i){
            return i.symbol.toLowerCase().indexOf(v.toLowerCase())!=-1
          })
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .main{
    box-sizing: border-box;
    margin-top: 60px;
    padding-bottom: 85px;
    width: 100%;
    font-family: 'Helvetica', 'Verdana', sans-serif;
    background-color: white;
  }
  .content{
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    text-align: justify;
    font-family: "Microsoft Yahei"
  }
  .items{
    border-top: solid 1px lightgray;
    text-align: left;
    line-height: 60px;
    width: 95%;
    height: 60px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: space-between;
  }
  .items:last-child{
    border-bottom: solid 1px lightgray;
  }
  .toolbar{
    text-align: left;
    line-height: 60px;
    width: 95%;
    height: 60px;
    display: flex;
    flex-direction: row;
    font-weight: bold;
    justify-content: space-between;
  }
/*  .indexAndName{
    width:45%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
  }*/
  .price,.cprice{
    width:30%;
    font-weight: bold;
    position: relative;
    overflow: hidden;
    cursor: pointer;
  }
  .cname,.cprice,.crate{
    cursor: pointer;
  }
  .cprice,.price{
    width:40%;
  }
  .name,.cname{
    width: 40%;
    position: relative;
  }
  .rate,.crate{
    width:20%;
  }
  .crate{
    font-weight: bold;
    align-self: right;
    position: relative;
  }
  .rate{
    align-self: center;
    height: 30px;
    line-height: 30px;
    color:white;
    font-size: 0.8em;
    text-align: center;
    border-radius: 2px;
  }
  .indexAndName,.market{
    height: 18px;
    line-height: 18px;
    font-size: 0.75em;
  }
  .market{
    color: grey;
  }
  .symbol{
    height:24px;
    line-height: 24px;
    font-weight: bold;
    font-size: 1.1em;
  }
  .index,.sname{
    display: inline-block;
    overflow: hidden;
    height:18px;
  } 

  .toolbar .up{
    position: absolute;
    bottom:14px;
    left: 50px;
    border-top: 6px solid whitesmoke;
    border-right:solid 6px transparent;
    border-left:solid 6px transparent;
    border-bottom: solid 6px transparent;
  }
  .toolbar .down{
    position: absolute;
    top:14px;
    left: 50px;
    border-top:solid 6px transparent;
    border-right:solid 6px transparent;
    border-left:solid 6px transparent;
    border-bottom: 6px solid whitesmoke;
  }
  .cprice .up,.cprice .down{
    left: 50px;
  }
  .cname .up,.cname .down{
    left: 50px;
  }
  input.search{
    height: 24px;
    border:solid 1px lightgrey;
    border-radius: 3px;
    float:right;
    align-self: flex-end; 
    /*margin-right: 10px;*/
    background-image: url('../../src/assets/toolbar/search.png'); 
    background-repeat: no-repeat;
    background-position: center right;
    background-clip: border-box;
    background-size:11%;
    box-sizing: border-box;
  }
  button.more{
    padding: .3em .8em;
    border: 1px solid #446d88;
    background: #58a linear-gradient(#77a0bb, #58a);
    border-radius: .2em;
    box-shadow: 0 .05em .25em gray;
    color: white;
    text-shadow: 0 -.05em .05em #335166;
    font-size: 125%;
    line-height: 1.5;
    margin-top: 12px;
    margin-bottom: 24px;
  }
  button.more:hover{
    box-shadow: 1px 3px 3px #ccc;
  }
  button.refresh{
    height: 24px;
    width: 60px;
    border:solid 1px lightgrey;
    border-radius: 3px;
    align-self: flex-start;
    box-sizing: border-box;
    /*margin-left: 8px;*/
  }
  .smallTool{
    width: 95%;
    margin:1px;
   /* display: flex;
    flex-direction: row;
    align-content: space-between;
    justify-content: space-between;*/
  }
  .navTop{
    background-image: url('../../static/imgs/navTop.png');
    background-size: cover;
    position: absolute;
    right: 12px;
    bottom: 97px;
    height: 35px;
    width:35px; 
  }
</style>



