<template>
  <div class="detail">
  
    <div class="container">
      <div class="brief">
        <div class="name">{{res.name}}({{res.symbol}})</div>
        <div class="price">${{res.price_usd}}
          <div class="rate" :style="{color:colorSet?(res.percent_change_24h<0?'#009900':'#CC3300'):(res.percent_change_24h>0?'#009900':'#CC3300')}">{{res.percent_change_24h>0?"+"+res.percent_change_24h:res.percent_change_24h}}%(24h)</div>
        </div>
      </div>
      <div class="intro">简介</div>
      <div class="rank">
        <div class="chRank">排名</div>
        <div class="numRank">{{res.rank}}</div>
      </div>
      <div class="market">
        <div class="chMarket">市值</div>
        <div class="numMarket">${{res.market_cap_usd|formatPrice}}</div>
      </div>
      <div class="change">
        <div class="chChange">成交量</div>
        <div class="numChange">${{res['24h_volume_usd']|formatPrice}}</div>
      </div>
      <div class="supply">
        <div class="chSupply">流通量</div>
        <div class="numSupply">${{res['available_supply']|formatPrice}} {{res.symbol}}</div>
      </div>
      <div class="website">网站</div>
      <div v-for="item in webs">
        <div class="webs">
          <div class="name">{{item.name}}</div>
          <div class="site"><a target="_blank" :href="item.site">{{item.site}}</a></div>
          <!-- <a href="{item.site}">{{item.site}}</a> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'detail',
    data () {
      return {
        greeting: 'this is the detail',
        version:"v0.7.8",
        author:"conanskyforce",
        motto:"然而，杰森郭达森早已看透了一切.",
        res:"",
        webs:[
        {
          name: "OKCoin",
          site: "https://www.okcoin.cn"
        },
        {
          name: "Btc-e",
          site: "https://www.btc-e.com"
        },
        {
          name: "Huobi",
          site: "https://www.huobi.com"
        },
        {
          name: "Bter",
          site: "https://www.bter.com"
        },
        {
          name: "CoinBase",
          site: "https://www.coinbase.com"
        },
        {
          name: "BTCChina",
          site: "https://www.btcchina.com"
        },
        {
          name: "btctrade",
          site: "https://www.btctrade.com"
        },
        {
          name: "Bt38",
          site: "https://www.Bt38.com"
        },
        {
          name: "Bithumb",
          site: "https://www.Bithumb.com"
        },
        {
          name: "Bitfinex",
          site: "https://www.Bitfinex.com"
        }
        ],
        colorSet:false
      }
    },
    created(){
      this.fetchData();
      var that = this;
      setTimeout(function(){
        that.navShow = true;
      }, 1000);
    },
    activated(){
      this.colorSet = localStorage.getItem('colorSet');
      if(this.colorSet=='false') this.colorSet=false;
    },
    filters:{
      formatPrice(n){
        var n = parseInt(n)
        var arr = (n+"").split("").reverse();
        var len= arr.length;
        var res = [];
        var i = 1;
        var z = 0;
        while(len>3*i){

        res = arr.splice(z+3*i++,0,',')
        z++;
        }
        return  arr.reverse().join("");
      }
    },
    watch:{
      '$route':'fetchData'
    },
    methods:{
      fetchData(){
        this.motto = this.$route.params.id;
        var that = this;
        var local = localStorage.getItem('result');
        var resString = JSON.parse(local);
        // console.log(resString);
        // console.log(this.$route.params.id)
        this.res = resString.filter(function(i){
          return i.id == that.$route.params.id;
        })[0];
        // console.log(this.res);
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* pages/details/details.wxss */
  .container{

    width: 90%;
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    /* border:solid 1px red; */

    margin:0 auto;
    margin-top:60px;
    margin-bottom: 85px;
    font-family: "Microsoft YaHei";
  }
  .intro,.website{
    background-color: lightgrey;
    color: grey;
    font-weight: bold;
    margin:0 -20px;
    text-indent: 20px;
    height: 42px;
    margin-bottom: -2px;
    line-height: 42px;
  }
  .brief{
    display: flex;
    flex-direction: column;
    width: 100%;
    font-size: 1.2em;
    margin-top: 25px;
    margin-bottom: 25px;
    align-items: flex-start;
    border-bottom:solid lightgrey 1px;
  }
  .price{
    font-size:1.55em;
    font-weight: bold;
  }
  .rate{
    display: inline-block;
    font-size: 0.5em;
  }
  .rank,.market,.change,.supply,.webs{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-top:solid lightgrey 1px;
    height: 42px;
    line-height: 42px;


    /* align-items: space-between; */
  }
/* .chRank,.chMarket,.chChange,.chSupply{
  align-self: flex-start;
}
.numRank,.numMarket,.numCHange,.numSupply{
  align-self: flex-end;
} */
.site{
  font-size: 0.85em;
  color: steelblue;
}
.site a:link,a:visited,a:hover,a:active{
  text-decoration: underline;
  color: steelblue;

}

</style>



