import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Article from '@/components/Article'
import Home from '@/components/Home'
import Detail from '@/components/detail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/main',
      name: 'Main',
      component: Main,
      // children:[
      // {
      //   path:"/main/:detail",
      //   component:Detail
      // }
      // ]
    },
    {
        path:"/main/:id",
        name:"Detail",
        component:Detail
      },
    {
      path: '/',
      redirect:'/main'
    },
    {
      path: '/articles',
      name: 'Article',
      component: Article
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    }
  ]
})



