<template>
  <div class="footer">
    <div class="main">
    <span :style="footerColor1">行  情</span>
    	<router-link  to="/main"></router-link>
    </div>
    <div class="articles">
    <span :style="footerColor2">新  闻</span>
    	<router-link  to="/articles"></router-link>
    </div>
    <div class="home">
    <span :style="footerColor3">我  的</span>
    	<router-link  to="/home"></router-link>
    </div>
    <!-- {{footerColor2?'#1296db':'balck'}} -->
    <!-- {{footer}} -->
  </div>
</template>

<script>
export default{
	name:'footer',
	data(){
		return {
			msg:"footer",
			footerColor1:true,
			footerColor2:false,
			footerColor3:false,
			res:'',
			footer:""
		}
	},
	methods:{

	}
	,
	// computed: {
	// 	styleObjet(){
	// 		return {
	// 			// 'background-image':this.isActive?:
	// 		}
	// 	}
	// },
	watch:{
  	'$route':"changeFooter"
	},
	methods:{
		changeFooter(){
			this.res = this.$route.name;
			// console.log(this.res)
			if(this.res=='Main') {
				this.footerColor1 = true;
				this.footerColor2 = false;
				this.footerColor3 = false;
			}
			else if(this.res=='Article') {
				this.footerColor1 = false;
				this.footerColor2 = true;
				this.footerColor3 = false;
			}
			else if(this.res=='Home') {
				this.footerColor1 = false;
				this.footerColor2 = false;
				this.footerColor3 = true;
			}
			// console.log(this.footerColor1)
		}
	},
	// computed:{
	// 	footer(){
	// 		return this.footerColor1;
	// 	},
	// 	footerColor1(){
	// 		return {
	// 			'color':(this.footerColor1?'#1296db':'balck')
	// 		}
	// 	},
	// 	footerColor2(){
	// 		return {
	// 			'color':(this.footerColor2?'#1296db':'balck')
	// 		}
	// 	},
	// 	footerColor3(){
	// 		return {
	// 			'color':(this.footerColor3?'#1296db':'balck')
	// 		}
	// 	}
	// }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.footer{
	background-color: whitesmoke;
	position:fixed;
	bottom:0px;
	width: 100%;
	height: 85px;
	overflow:hidden;
	display: flex;
	border-top: 1px lightgrey solid;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;
}
.main,.articles,.home{
	height: 70px;
	width: 100px;
	/*background-color: red;*/
	text-align: center;
	line-height: 65px;
}
.footer a{
	display: block;
	height: 100%;
	width: 100%;
}
.main a{
	background-image: url('../../src/assets/toolbar/main.png');
	background-size: 35px 35px;
	background-repeat: no-repeat;
	background-position: center;
}
.articles a{
	background-image: url('../../src/assets/toolbar/articles.png');
	background-size: 35px 35px;
	background-repeat: no-repeat;
	background-position: center;
}
.home a{
	background-image: url('../../src/assets/toolbar/home.png');
	background-size: 35px 35px;
	background-repeat: no-repeat;
	background-position: center;
}
/*active*/
.main a.router-link-exact-active{
	background-image: url('../../src/assets/toolbar/main-s.png');
}
.articles a.router-link-exact-active{
	background-image: url('../../src/assets/toolbar/articles-s.png');
}
.home a.router-link-exact-active{
	background-image: url('../../src/assets/toolbar/home-s.png');
}
.main,.articles,.home{
	position: relative;
	/*border:solid 1px red;*/
}
.footer span{
	position: absolute;
	display: block;
	height: 24px;
	font-size: 10px;
	bottom: 18px;
	left: 50%;
	width:24px;
	z-index: -1;
	transform: translateX(-50%);
}
</style>



