<template>
  <div class="header">
  <div class="navToMain" v-show="navShow">
    <router-link to='/main'></router-link> 
  </div>
    {{res}}
  </div>
</template>

<script>
export default {
  name: 'header',
  data(){
  	return {
  		title:"Welcome to use BitInfo！",
  		res:"",
      navShow:false
  	}
  },
  created(){
  	this.res = this.$route.name;
  	// console.log(this.$route.name);

  },
  watch:{
  	'$route':"changeHeader"
  },
  methods:{
  	changeHeader(){
  		this.res = this.$route.name;
  		// console.log(this.$route)
  		if(this.res=='Detail') this.navShow = true;
  		else this.navShow = false;
  	}
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header{
	background-color: whitesmoke;
	position:fixed;
	top:0px;
	width: 100%;
	z-index: 99;
	height: 60px;
	line-height: 60px;
	text-align: center;
	max-height: 100px;
	overflow:hidden;
}
.navToMain{
  position: absolute;
  top:18px;
  left:18px;
  height: 24px;
  width:24px;
  /*background-color: red;*/
  z-index: 120;
  background-image: url(/static/imgs/back.png);
  background-size: cover;
  background-repeat: no-repeat;
}
.navToMain a{
  display: inline-block;
  height: 24px;
  width:24px;
}
</style>



