<template>
  <div class="home">
    <div class="user">
      <div class="avatar">
        <img src='/static/imgs/_1avatar.jpg' alt="avatar">
      </div>
      <div class="motto">
        {{motto}}
      </div>
    </div>
    <div class="columns">
      <div class="chooseing">
        <div class="chChooseing">自选</div>
        <div class="reChooseing">{{chooseing}}</div>
        </div>
        <div class="setting">
          <div class="chSetting">默认展示</div>
          <div class="reSetting">{{setting}}</a></div>
        </div>
        <div class="version">
          <div class="chVersion">红涨绿跌</div>
          <div :class="[reVersion,{check:checked}]" @click.capture="setColor"><input v-model="checked" type="checkbox"></div>
        </div>
        <div class="author">
          <div class="chAuthor">关于</div>
          <div class="reAuthor" ><a :href="href" target="_blank">{{author}}</a></div>
        </div>
        <div class="random">{{random}}</div>
      </div>
    </div>
  </template>

  <script>
    export default {
      name: 'home',
      data () {
        return {
          chooseing: '欢迎使用BitInfo!',
          version:"v0.8.2",
          author:"conanskyforce",
          motto:"然而，杰森郭达森早已看透了一切.",
          href:"https://github.com/conanskyforce",
          random:"“最好的投资，就是教育”",
          setting:"TOP 20",
          check:"check",
          checked:false,
          reVersion:"reVersion"
        }
      },
      created(){
        var c = localStorage.getItem('colorSet');
        if(!c||c=='false'){
          this.checked=false;
        }else{
          this.checked = true;
        }
      },
      methods:{
        setColor(e,){
          // console.log(e)
          // console.log(this.$data)
          // bus.$emit('is-selected', this.checked)
          localStorage.setItem('colorSet',!this.checked)
          // console.log(this.checked)
        }
      }
    }
  </script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
    .home{
      box-sizing: border-box;
      padding-top:60px;
      padding-bottom: 85px;
      height: auto;
      width: 100%;
      display: flex;
      flex-direction: column;
      /*justify-content: center;*/
      align-items: center;
      background-color: whitesmoke;
    }
    .user{
      display: flex;
      box-sizing: border-box;
      flex-direction: column;
      align-items: center;
      background-color: lightgrey;
      /*padding-bottom: 20px;*/
      /*margin-left:-100px;*/
      width: 100%;
      height: 220px;
      background-image: url(/static/imgs/background.jpg);
      background-size: cover;
      /*margin-right:-100px;*/
      margin-bottom: 25px;
    }
    .avatar,.avatar img{
      width: 85px;
      height: 85px;
      border-radius: 50%;
      margin-top: 15px;
      margin-bottom: 6px;
    }
    .motto{
      margin-top: 25px;
      font-size: 1.1em;
      width: 65%;
      font-family:"Microsoft YaHei" 'Helvetica', 'Verdana', sans-serif;
      text-align: center;
      font-style: italic;
      font-weight: 600;
      position: relative;
    }
    .motto:before{
      position: absolute;
      top:-22px;
      left: -2px;
      margin-left: -12px;
      content: ",,";
      height: 24px;
      width: 24px;
      font-weight: bold;
      font-size: 1.6em;
      color:lightgrey;
      /*background-color: red;*/
    }
    .chooseing,.version,.author,.setting{
      text-align: center;
      margin-bottom: 10px;
      height: 45px;
      line-height: 45px;
      width: 100%;
      background-color: white;
      display: flex;
      flex-direction: row;
      box-sizing: border-box;
      padding-left: 20px;
      padding-right: 20px;
      box-sizing: border-box;
      justify-content: space-between;
      border-top:1px solid whitesmoke;
    }
    .reChooseing,.chChooseing,.zhVersion,.reVersion,.chAuthor,.reAuthor,.chSetting,.reSetting{
      line-height: 45px;
    }    
    .author{
      margin-bottom: px;
    }
    .random{
      margin-top: 2px;
      font-size: 1.1em;
      width: 65%;
      font-family:"Microsoft YaHei" 'Helvetica', 'Verdana', sans-serif;
      text-align: center;
      font-style: italic;
      font-weight: 600;
      position: relative;
      align-self: center;
    }
    .columns{
      display: flex;
      width: 100%;
      flex-direction: column;
      justify-content: space-between;
    }
    .reVersion,input{
      height: 45px;
      width: 60px;
      background-image: url('/static/imgs/st-c.png');
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center right;
    }
    .reVersion.check{
      background-image: url('/static/imgs/st-o.png');
    }
    .reVersion{
      z-index: 999
    }
    input{
      z-index: -111;
      height: 25px;
      width: 25px;
      opacity: 0;
      filter: alpha(opacity=0);
      /*display: none;*/
    }
  </style>



