<template>
  <div class="article">
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide"><img src='/static/news/virtual.jpg' alt="virtual"></div>
        <div class="swiper-slide"><img src='/static/news/database.jpg' alt="database"></div>
        <div class="swiper-slide"><img src='/static/news/blockchain.jpg' alt="blockchain"></div>
      </div>
      <div class="swiper-pagination"></div>
    </div>
    <!-- content -->
    <div class="contents" v-for="content in contents">
      <div class="inner">
        <div class="thumbnail">
          <img :src="content.src" :alt="content.title"></img>
        </div>
        <div class="cc">
          <div class="title">{{content.title}} ,</div>
          <div class="author">{{content.author}} ,{{content.date}}</div>
          <div class="content">{{content.content}}</div> 
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  export default {
    name: 'article',
    data () {
      return {
        msg: 'Welcome to Your article!',
        contents: [
        {
          'title': "区块链",
          'src':'/static/news/blockchain.jpg',
          'author': "conan",
          'content': "区块链是分布式数据存储、点对点传输、共识机制...",
          'date': "2017-2-11"
        },
        {
          'title': "数据库",
          'src': '/static/news/database.jpg',
          'author': "conan",
          'content': "数据库(Database)是按照数据结构来组织、存储和管理数据的仓库...",
          'date': "2017-4-16"
        },
        {
          'title': "加密算法",
          'src': '/static/news/crypto.jpg',
          'author': "conan",
          'content': "数据加密的基本过程就是对原来为明文的文件或数据按某种算法进...",
          'date': "2017-5-21"
        },
        {
          'title': "虚拟化",
          'src': '/static/news/virtual.jpg',
          'author': "conan",
          'content': "虚拟化，是指通过虚拟化技术将一台计算机虚拟...",
          'date': "2017-6-11"
        },
        {
          'title': "算法",
          'src': '/static/news/algorithm.jpg',
          'author': "conan",
          'content': "算法（Algorithm）是指解题方案的准确而完整的描...",
          'date': "2017-7-29"
        }
        ]
      }
    },
    mounted:function(){
      // console.log(Swiper);
      var mySwiper = new Swiper ('.swiper-container', {
        // direction: 'vertical',
        loop: true,
        // 如果需要分页器
        pagination: '.swiper-pagination',
        
        // 如果需要前进后退按钮
        nextButton: '.swiper-button-next',
        prevButton: '.swiper-button-prev',
        
        // 如果需要滚动条
        // scrollbar: '.swiper-scrollbar'
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .article{
    box-sizing: border-box;
    margin-top:60px;
    margin-bottom: 85px;
    height: auto;
    width: 100%;
    background-color: whitesmoke;
  }
  .contents{
    display: flex;
    margin-bottom: 12px;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    /* border:solid 1px lightgrey; */
    width: 100%;
    height: 100px;
    background-color: white;
  }
  .inner{
    display: flex;
    flex-direction: row;
  }
  .thumbnail img{
    display: inline-block;
    height: 80px;
    width: 80px;
    margin-left: 20px;
    /* border:solid 1px red; */
  }
  .cc{
    /* border: solid 1px yellow; */
    margin-left: 20px;
    padding-top: 8px;
    height: 80px;
    width: 65%;
    font-size: 0.8em;
    align-self: right;
    overflow: hidden;
    border-radius: 2px;
  }
  .title,.author{
    display: inline-block;
    font-weight: bold;
  }
  .content{
    text-indent: 2px;

    font-family: "Microsoft Yahei";
    text-overflow:clip;
    /* white-space:nowrap;  */
  }
  .swiper-container{
    height: 120px;
    margin-bottom: 12px;
  }
/*  .swiper-slide:first-child{
    background-image: url('../assets/news/crypto.jpg');
  }*/
  .swiper-wrapper img{
    height: 100%;
    width: 100%;
  }
  /*::-webkit-scrollbar{  
    width: 16px;  
    height: 16px;  
    background-color: #F5F5F5;  
  }  
  ::-webkit-scrollbar-track{  
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);  
    border-radius: 4px;  
    background-color: #F5F5F5;  
  }  
  ::-webkit-scrollbar-thumb{  
    border-radius: 4px;  
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);  
    background-color: #555;  
  }*/

</style>




